<template>
    <v-app>
        <div class="area">
            <ul class="circles">
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
        </div>

        <v-container fluid class="fill-height" style="z-index:1">
            <v-row dense align="center" justify="center">
                <v-col cols="12" md="3">
                    <v-card 
                        :elevation="4" 
                        height="400px" 
                        class="text-center px-8 py-10 bg-color"
                    >
                        <v-icon x-large class="mt-8">mdi-account-circle</v-icon>
                        <h3 class="mt-2">Software Login</h3>
                        <v-form ref="loginForm" @submit.prevent="login">
                            <v-text-field 
                                
                                autofocus
                                prepend-inner-icon="mdi-account"
                                class="mt-4"
                                prefix=" | "
                                v-model="user.username"
                                :rules="[() => !!user.username || '']"
                            >
                            </v-text-field>
                            <v-text-field 
                                prepend-inner-icon="mdi-key"
                                class="mt-2"
                                prefix=" | "
                                type="password"
                                v-model="user.password"
                                :rules="[() => !!user.password || '', validatePassword.length]"
                            >
                            </v-text-field>
                            <v-btn block type="submit" color="primary" class="mt-4" :loading="loading">Login</v-btn>
                        </v-form>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
    </v-app>
</template>

<script>
import utilities from '../mixins/utility.mixin'
export default {
    name: "Login",

    mixins: [utilities],
    data: () => {
        return {
            user: {
                username: '',
                password: '',
            },
            loading: false
        }
    },
    
    mounted() {
        let jwtToken = localStorage.getItem('jwtToken')
        if (jwtToken != null && jwtToken != '') this.$router.push('/')
    },

    methods: {
        async login() {
            let isValid = true;

            this.$refs.loginForm.inputs.forEach(input => {
                if(input.hasError) {
                    isValid = false;
                    return;
                }
            })

            if(!isValid) {
                return;
            }

            this.loading = true;
            let isSuccess = await this.$store.dispatch('user/login', this.user);
            if(isSuccess) {
                this.$router.push('/');
            }
            this.loading = false;
        }
    }
};
</script>

<style lang="scss" scoped>
* {
    margin: 0px;
    padding: 0px;
}
.bg-color {
    background: linear-gradient(to right,  #72fcd4, #ffffff);
}
.area {
    background: #292961;
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    z-index: 0;
}

.circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;
}

.circles li:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}

.circles li:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}
</style>